import { useDispatch } from 'react-redux';
import Swal from 'sweetalert2';
import { startRegister } from '../../actions/auth';
import { useForm } from '../../hooks/useForm';
import "./login.css";

export const RegisterScreen = () => {


  const dispatch = useDispatch();

  const [inputValues, handleInputChange] = useForm({
    name: 'Pedro',
    email: 'peter@example.com.mx',
    password: '123456',
    password2: '123456'
  });


  const {name, email, password, password2} = inputValues;


  const handleRegister = (e) => {
    e.preventDefault();

    if(password !== password2){
      return Swal.fire('Error', 'Las contraseas no coinciden', 'error');
    }

    dispatch(startRegister(name, email, password));

  }

  return (
    <div className="container login-container">
      <div className="row">
        <div className="col-md-12 login-form-2">
          <h3>Registro</h3>
          <form onSubmit={handleRegister}>
            <div className="form-group mb-3">
              <input
                type="text"
                className="form-control"
                placeholder="Nombre"
                name="name"
                value={name}
                onChange={handleInputChange}
              />
            </div>
            <div className="form-group mb-3">
              <input
                type="email"
                className="form-control mb-3"
                placeholder="Correo"
                name="email"
                value={email}
                onChange={handleInputChange}
              />
            </div>
            <div className="form-group mb-3">
              <input
                type="password"
                className="form-control"
                placeholder="Contrasea"
                name="password"
                value={password}
                onChange={handleInputChange}
              />
            </div>

            <div className="form-group">
              <input
                type="password"
                className="form-control"
                placeholder="Repita la contrasea"
                name="password2"
                value={password2}
                onChange={handleInputChange}
              />
            </div>

            <div className=" mt-4 d-grid gap-2">
              <button type="submit" className="btn btn-primary btnSubmit">
                Crear Cuenta
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
};
