import { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';

import Modal from 'react-modal';
import DateTimePicker from 'react-datetime-picker';
import moment from 'moment';
import Swal from 'sweetalert2';

import { eventClearActiveEvent, eventStartUpdate, startAddNew } from '../../actions/events';
import { uiCloseModal } from '../../actions/ui';

const customStyles = {
    content: {
      top: '50%',
      left: '50%',
      right: 'auto',
      bottom: 'auto',
      marginRight: '-50%',
      transform: 'translate(-50%, -50%)',
    },
};

Modal.setAppElement('#root');

const now = moment().minutes(0).seconds(0).add(1, 'hours');
const nowPlus1 = now.clone().add(1, 'hours');

const initEvent = {
    title: '',
    notes: '',
    start: now.toDate(),
    end: nowPlus1.toDate(),
};

export const CalendarModal = () => {


    const dispatch = useDispatch();
    const { modalOpen } = useSelector(state => state.ui);
    const { activeEvent } = useSelector(state => state.calendar);

    // const [dateStart, setDateStart] = useState(now.toDate());
    // const [dateEnd, setDateEnd] = useState(nowPlus1.toDate());
    const [titleValid, setTitleValid] = useState(true);

    const [formValues, setFormValues] = useState(initEvent);


    const {title, notes, start, end} = formValues;


    useEffect(() => {

        if (activeEvent) {
            setFormValues(activeEvent);
        } else {
            setFormValues(initEvent);
        }

    }, [activeEvent]);


    const handleInputChange = ({ target }) => {
        setFormValues({
            ...formValues,
            [target.name]: target.value,
        });
    }

    const closeModal = () =>{
        dispatch(uiCloseModal());
        dispatch(eventClearActiveEvent())
        setFormValues(initEvent);
    }

    const handleStartDateChange = (e) =>{
        // setDateStart(e);
        setFormValues({
            ...formValues,
            start: e,
        });
    }

    const handleEndDateChange = (e) =>{
        // setDateEnd(e);
        setFormValues({
            ...formValues,
            end: e,
        });
    }


    const handleSubmitFormEvent = (e) => {
        e.preventDefault();
        
        const momentStart = moment(start);
        const momentEnd = moment(end);

        if( momentStart.isSameOrAfter(momentEnd) ){
            Swal.fire('Error', 'fecha de fin no puede ser igual que la fecha de inicio', 'error');
            return;
        }

        if(title.trim().length < 2){
            Swal.fire('Error', 'el titulo debe tener al menos 3 caracteres', 'error');
            return setTitleValid(false);
        }


        if(activeEvent){
            dispatch(eventStartUpdate(formValues));
        }else {
            dispatch(startAddNew(formValues));
        }

        

        setTitleValid(true);
        closeModal();
    }

    return (
        <Modal
            isOpen={ modalOpen }
            // onAfterOpen={ afterOpenModal }
            onRequestClose={ closeModal }
            style={ customStyles }
            closeTimeoutMS={ 200 }
            className="modal"
            overlayClassName="modal-fondo"
        >
            
            <h1>
                {
                    activeEvent ? 'Editar evento' : 'Nuevo evento'
                }
            </h1>
            <hr />
            <form 
                className="container"
                onSubmit={ handleSubmitFormEvent }
            >

                <div className="form-group mb-3">
                    <label>Fecha y hora inicio</label>
                    <DateTimePicker
                        format="dd/MM/y h:mm:ss a"
                        locale='es-MX'
                        onChange={ handleStartDateChange } 
                        // value={ dateStart }
                        value={ start }
                        className="form-control"
                    />
                </div>

                <div className="form-group mb-3">
                    <label>Fecha y hora fin</label>
                    <DateTimePicker
                        format="dd/MM/y h:mm:ss a"
                        locale='es-MX'
                        onChange={ handleEndDateChange } 
                        // value={ dateEnd }
                        // minDate={ dateStart }
                        value={ end }
                        minDate={ start }
                        className="form-control"
                    />
                </div>

                <hr />
                <div className="form-group mb-3">
                    <label>Titulo y notas</label>
                    <input 
                        type="text" 
                        className={ `form-control ${ !titleValid && 'is-invalid' }` }
                        placeholder="Ttulo del evento"
                        name="title"
                        autoComplete="off"
                        value={title}
                        onChange={ handleInputChange }
                    />
                    <small id="emailHelp" className="form-text text-muted">Una descripcin corta</small>
                </div>

                <div className="form-group mb-3">
                    <textarea 
                        type="text" 
                        className="form-control"
                        placeholder="Notas"
                        rows="5"
                        name="notes"
                        value={notes}
                        onChange={ handleInputChange }
                    ></textarea>
                    <small id="emailHelp" className="form-text text-muted">Informacin adicional</small>
                </div>

               <div className="d-grid gap-2">
                    <button
                        type="submit"
                        className="btn btn-outline-primary btn-block"
                    >
                        <i className="far fa-save"></i>
                        <span> Guardar</span>
                    </button>
               </div>

            </form>

        </Modal>
    )
}
